import React from 'react'
import DeleteIcon from '@material-ui/icons/Delete';

function Row({isFirst, activity, size, index }) {
    const createRow = n => {
        const cells = [];
        for(let i=0; i<n; i++) {
            cells.push(
                isFirst ? (
                <td>{i+1}</td>
                ) : (
                <td style={{cursor: "pointer"}} onClick={e => e.target.style.backgroundColor="green"}> </td>
                )
            )
        }
        return cells;
    }
    return (
        <div className="row">
           <table>
               <tr>
                    <th>{activity}</th>
                    {createRow(size)}
               </tr>
           </table>
           {isFirst ? null : <DeleteIcon style={{cursor: "pointer"}} />}
        </div>
    )
}

export default Row
